import React from "react";
import { faBan, faComments } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Menu = () => {
  const blog = () => {
    window.location.href = "https://runningboy02.tistory.com/";
  };
  const ban = () => {
    window.location.href = "https://kr.pe/m7j";
  };
  return (
    <>
      <span class="center buk" Style="font-size: 50px;" onClick={blog}>
        <FontAwesomeIcon
          title="Home"
          icon={faComments}
          color={"white"}
          size="1x"
        />
        블로그 바로가기
      </span>
      <span class="center buk" Style="font-size: 50px;" onClick={ban}>
        <FontAwesomeIcon title="Home" icon={faBan} color={"white"} size="1x" />
        회원 탈퇴 및 비밀번호 변경요청 바로가기
      </span>
    </>
  );
};
export default Menu;
