import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTwitter, faSpeakerDeck } from "@fortawesome/free-brands-svg-icons";
import {
  faUser,
  faStickyNote,
  faTools,
  faRedo,
  faComments,
  faCompass,
} from "@fortawesome/free-solid-svg-icons";
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";

const Navigation = ({ userObj }) => {
  return (
    <nav>
      <ul style={{ display: "flex", justifyContent: "center", marginTop: 50 }}>
        <li>
          <Link
            to="/"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="Home"
              icon={faTwitter}
              color={"red"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>위대한 인민들의 소통망</span>
          </Link>
        </li>
        <li>
          <Link
            to="/profile"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="profile"
              icon={faUser}
              color={"#04AAFF"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>
              {userObj.displayName
                ? `${userObj.displayName}의 프로필`
                : "프로필"}
            </span>
          </Link>
        </li>
        <li>
          <Link
            to="/note"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="release note"
              icon={faTools}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>패치 보고서</span>
          </Link>
        </li>
        <li>
          <Link
            to="/notice"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="Notice"
              icon={faStickyNote}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>공지사항</span>
          </Link>
        </li>
        <li>
          <Link
            to="/menu"
            style={{
              marginLeft: 10,
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              fontSize: 12,
            }}
          >
            <FontAwesomeIcon
              title="menu"
              icon={faCompass}
              color={"#fff"}
              size="2x"
            />
            <span style={{ marginTop: 10 }}>메뉴</span>
          </Link>
        </li>
        {/* 모바일이면 보이게 pc는 안보이게*/}
        <MobileView>
          <li>
            <Link
              to={"/rrd?" + window.location.pathname}
              style={{
                marginLeft: 10,
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                fontSize: 12,
              }}
            >
              <FontAwesomeIcon
                title="reload"
                icon={faRedo}
                color={"#fff"}
                size="2x"
              />
              <span style={{ marginTop: 10 }}>새로고침</span>
            </Link>
          </li>
        </MobileView>
      </ul>
    </nav>
  );
};
export default Navigation;
